import React from 'react';
import { Block, Controller, RenderNodeProps } from '@ali/4ever-cangjie';
import { VoteCardPlugin } from '@ali/4ever-bamboo';

type VotePluginConfig = VoteCardPlugin.VotePluginConfig;

interface IProps {
  nodeProps: RenderNodeProps<Block>;
  controller: Controller;
  voteConfig: VotePluginConfig;
  isPublish?: boolean;
}

export const Vote: React.FC<IProps> = ({
  nodeProps,
  controller,
  voteConfig,
  isPublish,
}) => {
  const {
    locale,
    getVote,
    onSocketMsg,
  } = voteConfig;

  const { node } = nodeProps;

  return (
    <VoteCardPlugin.Container
      locale={locale}
      node={node}
      getVote={getVote}
      controller={controller}
      onSocketMsg={onSocketMsg}
      isPublish={isPublish}
      readOnly
    />
  );
}
